'use client';

import { useState } from 'react';

const chains = ['Ethereum', 'Polygon', 'Arbitrum', 'Base'];
const tokens = ['USDC', 'DAI', 'ETH', 'WETH'];

export default function SwapWidget() {
  const [sourceChain, setSourceChain] = useState('Ethereum');
  const [destChain, setDestChain] = useState('Polygon');
  const [sourceToken, setSourceToken] = useState('ETH');
  const [destToken, setDestToken] = useState('USDC');
  const [amount, setAmount] = useState('');

  const handleSwap = () => {
    alert(`Swapping ${amount} ${sourceToken} on ${sourceChain} to ${destToken} on ${destChain}`);
    // You can trigger swap logic here
  };

  return (
    <div className="max-w-lg mx-auto mt-10 p-6 bg-white rounded-2xl shadow-xl border space-y-4">
      <h2 className="text-2xl font-semibold text-center">Token Swap</h2>

      {/* Source */}
      <div className="space-y-2">
        <label className="block text-sm font-medium text-gray-700">From Chain</label>
        <select
          value={sourceChain}
          onChange={(e) => setSourceChain(e.target.value)}
          className="w-full p-2 border rounded-lg"
        >
          {chains.map((chain) => (
            <option key={chain}>{chain}</option>
          ))}
        </select>

        <label className="block text-sm font-medium text-gray-700">Token</label>
        <select
          value={sourceToken}
          onChange={(e) => setSourceToken(e.target.value)}
          className="w-full p-2 border rounded-lg"
        >
          {tokens.map((token) => (
            <option key={token}>{token}</option>
          ))}
        </select>

        <label className="block text-sm font-medium text-gray-700">Amount</label>
        <input
          type="number"
          min="0"
          placeholder="0.0"
          value={amount}
          onChange={(e) => setAmount(e.target.value)}
          className="w-full p-2 border rounded-lg"
        />
      </div>

      {/* Destination */}
      <div className="space-y-2">
        <label className="block text-sm font-medium text-gray-700">To Chain</label>
        <select
          value={destChain}
          onChange={(e) => setDestChain(e.target.value)}
          className="w-full p-2 border rounded-lg"
        >
          {chains.map((chain) => (
            <option key={chain}>{chain}</option>
          ))}
        </select>

        <label className="block text-sm font-medium text-gray-700">Token</label>
        <select
          value={destToken}
          onChange={(e) => setDestToken(e.target.value)}
          className="w-full p-2 border rounded-lg"
        >
          {tokens.map((token) => (
            <option key={token}>{token}</option>
          ))}
        </select>
      </div>

      {/* Swap button */}
      <button
        onClick={handleSwap}
        className="w-full py-3 bg-black text-white rounded-lg hover:bg-gray-800 transition"
      >
        Swap
      </button>
    </div>
  );
}

